.card {
  display: flex;
  align-items: center;
  width: 80%;
  position: relative;
  margin: 20px auto;
  justify-content: space-between;
  border-bottom: 1px solid grey;
}

.cardimage {
  width: 350px;
  height: 250px;
  border-radius: 10px;
}

.card-text {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.location {
  display: flex;
  width: 50%;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
}

.location a {
  text-decoration: none;
  color: grey;
}

.location-logo {
  color: tomato;
  padding: 0.5rem;
}

.title {
  margin: 0.5rem 0 0.5rem 0;
  font-size: 2rem;
}

.date {
  margin: 0.3rem 0;
  font-size: 1rem;
}

.description {
  font-size: 16px;
}
